body {
  background-color: black;
}

.App .table {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App .table .row {
  display: flex;
}
.App .table .row .cell {
  background-color: rgba(255, 0, 0, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.App .table .row .cell .disk {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: blueviolet;
}/*# sourceMappingURL=App.css.map */